import React, { useCallback, useState } from 'react';
import { Button, Divider, notification, Space } from 'antd';
import PointsViewer from '../../components/PointsViewer';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import getSkill from '../../getSkill';
import AddDialog from './panels/AddDialog';
import ResetAlert from '../../components/ResetAlert';
import SkillName from '../../components/SkillName';

const SelectSkill = ({ value = {}, defaultValue = {}, onDone }) => {
  const [onlineSkills, setOnlineSkills] = useState(value.onlineSkillList);
  const [restPoints, setRestPoints] = useState(value.points);
  const [showAdd, setShowAdd] = useState(false);
  const [showWarning, setShowWarning] = useState(false);
  const [api, contextHolder] = notification.useNotification();

  const openNotification = useCallback(description => api.info({ message: '警告', description, placement: 'top' }), [api]);

  const deleteSkill = useCallback(id => setOnlineSkills(prev => prev.filter(skill => skill.id !== id)), []);
  const handleReset = useCallback(() => {
    setRestPoints(defaultValue.points);
    setOnlineSkills(defaultValue.onlineSkillList);
    setShowWarning(false);
  }, [defaultValue]);

  const handleDone = useCallback(() => {
    setShowAdd(false);
    onDone({ onlineSkillList: onlineSkills, points: restPoints });
  }, [onlineSkills, restPoints, onDone]);
  const handleAdd = useCallback(
    result => {
      if (onlineSkills.some(({ id }) => id === result.id)) {
        openNotification('已經有相同領域，因此會新的同領域覆蓋');
        setOnlineSkills(prev => [...prev.filter(skill => skill.id !== result.id), { id: result.id, cost: result.cost }]);
      } else {
        setOnlineSkills(prev => [...prev, { id: result.id, cost: result.cost }]);
      }
      setRestPoints(result.restPoints);
      setShowAdd(false);
    },
    [onlineSkills, openNotification]
  );

  return (
    <div>
      {contextHolder}
      <h2>參數設定</h2>
      <Space direction='vertical'>
        <Space>
          {'當前點數'}
          <PointsViewer points={restPoints} />
        </Space>
        {onlineSkills.map(({ id, cost }) => {
          const skill = getSkill(id);
          return (
            <Space key={id.toString()}>
              <SkillName skill={skill} cost={cost} />
              <PointsViewer points={cost} />
              <MinusCircleOutlined onClick={() => deleteSkill(id)} />
            </Space>
          );
        })}
        <Button type='dashed' onClick={() => setShowAdd(true)} block icon={<PlusOutlined />}>
          Add Skill
        </Button>
        <Divider />
        <Space>
          <Button onClick={() => setShowWarning(true)}>重設</Button>
          <Button onClick={() => handleDone()}>確認</Button>
        </Space>
      </Space>
      <AddDialog points={restPoints} open={showAdd} onOk={handleAdd} onCancel={() => setShowAdd(false)} />
      <ResetAlert open={showWarning} onOk={handleReset} onCancel={() => setShowWarning(false)} />
    </div>
  );
};
export default SelectSkill;
