import React from 'react';
import { Button, Checkbox, Space } from 'antd';
import SkillName from '../components/SkillName';
import getSkill from '../getSkill';
import effectRange from '../config/effectRange.json';
import targetCount from '../config/targetCount.json';
import { useCallback, useEffect, useState } from 'react';
import ResetAlert from '../components/ResetAlert';

const CheckedHuman = ({ name, id, checked, onChange }) => (
  <Space>
    <Checkbox checked={checked} onChange={e => onChange({ id, checked: e.target.checked })} />
    {name}
  </Space>
);

const createData = value => {
  const { onlineSkillList, charactorList } = value || {};
  return onlineSkillList.map(({ id, cost }) => {
    const { count } = targetCount;
    const aroundList = charactorList.filter(({ around }) => around);
    if (aroundList.length === 1)
      return {
        id,
        target: [{ id: aroundList[0].id, checked: true }],
        cost,
      };
    else {
      // 亂數建立 array，並且取前 count 個
      const tmp = new Array(aroundList.length)
        .fill()
        .map((_, i) => i)
        .sort(() => (Math.random() > 0.5 ? 1 : -1));
      const target = tmp.slice(0, count).map(index => {
        const human = aroundList[index];
        return { id: human.id, checked: true };
      });
      return { id, target, cost };
    }
  });
};
const SkillEffect = ({ value, defaultValue, onDone }) => {
  const [data, setData] = useState([]);
  const [showWarning, setShowWarning] = useState(false);
  useEffect(() => {
    setData(createData(value));
  }, [value]);

  const handleChange = useCallback(
    (skillId, charactorId, checked) =>
      setData(prev => {
        const clone = [...prev];
        const skillIndex = clone.findIndex(({ id }) => id === skillId);

        clone.splice(skillIndex, 1, {
          ...clone[skillIndex],
          target: [...clone[skillIndex].target].map(human => {
            if (human.id === charactorId) return { ...human, checked };
            return human;
          }),
        });
        return clone;
      }),
    []
  );

  const handleReset = useCallback(() => {
    setData(createData(defaultValue));
    setShowWarning(false);
  }, [defaultValue]);
  const handleDone = useCallback(() => {
    onDone(
      data.map(skillEffect => ({
        id: skillEffect.id,
        target: skillEffect.target,
      }))
    );
  }, [data, onDone]);

  return (
    <Space direction='vertical'>
      <h2>確認影響對象</h2>
      <h3>{`影響範圍為 ${effectRange.range}m`}</h3>
      {data.map(({ id, target, cost }) => {
        const skill = getSkill(id);
        return (
          <Space key={id.toString()}>
            <SkillName skill={skill} cost={cost} />
            {target.map(info => {
              const charactor = value.charactorList.find(charactor => charactor.id === info.id);
              return (
                <CheckedHuman
                  key={charactor.id}
                  name={charactor.name}
                  checked={info.checked}
                  onChange={result => handleChange(id, charactor.id, result.checked)}
                />
              );
            })}
          </Space>
        );
      })}
      <Space>
        <Button onClick={() => setShowWarning(true)}>重設</Button>
        <Button onClick={() => handleDone()}>確認</Button>
      </Space>
      <ResetAlert open={showWarning} onOk={handleReset} onCancel={() => setShowWarning(false)} />
    </Space>
  );
};
export default SkillEffect;
