import { useCallback, useState } from 'react';
import pointConfig from './config/pointConfig.json';
import useHistory from './useHistory';

const initState = {
  points: { sky: 0, ground: 0, human: 0 },
  charactorList: [],
  geograph: false,
  onlineSkillList: [],
  skillEffectList: [],
};

const pointGrow = (point, max) => (point + 1 > max ? max : point + 1);

// 行為操作，將會被記錄於 history
const useAction = () => {
  const [data, setData] = useState({ ...initState });
  const { appendHistory } = useHistory();

  /**
   * 進入 init 階段時，根據道具是否展開決定是否獲得 point
   */
  const initStep = useCallback(() => {
    setData(prev => {
      if (prev.geograph === false) {
        appendHistory({ step: 'init', action: 'init', value: {} });
        return prev;
      }
      const points = ['sky', 'ground', 'human'].reduce(
        (result, key) => ({
          ...result,
          [key]: pointGrow(prev.points[key], pointConfig[key]),
        }),
        { sky: 0, ground: 0, human: 0 }
      );
      appendHistory({ step: 'init', action: 'init', value: { points } });
      return { ...prev, points };
    });
  }, [appendHistory]);

  /**
   * 切換 geograph
   * 關閉時清空，開啟時點數均為1
   */
  const switchGeograph = useCallback(() => {
    setData(prev => {
      const value = {
        geograph: !prev.geograph,
        points: ['sky', 'ground', 'human'].reduce((result, key) => ({ ...result, [key]: prev.geograph ? 0 : 1 }), {
          sky: 0,
          ground: 0,
          human: 0,
        }),
        onlineSkillList: [],
      };
      appendHistory({ step: 'switchGeograph', action: 'switchGeograph', value });

      return { ...prev, ...value };
    });
  }, [appendHistory]);

  /**
   * 設定技能，包含:
   * 移除發動中技能、新增技能與其 cost
   * 其中，同個 skillId 會被新的覆蓋
   */
  const setSkill = useCallback(
    (onlineSkillList, points) => {
      setData(prev => ({ ...prev, onlineSkillList, points }));
      appendHistory({ step: 'selectSkill', action: 'setSkill', value: { onlineSkillList } });
    },
    [appendHistory]
  );

  /**
   * update charactor list
   */
  const setCharactorList = useCallback(
    charactorList => {
      setData(prev => ({ ...prev, charactorList }));
      appendHistory({ step: 'setCharactor', action: 'setCharactorList', value: { charactorList } });
    },
    [appendHistory]
  );

  /**
   * decide the skill work or cancel
   */
  const setSkillEffect = useCallback(
    skillEffectList => {
      setData(prev => ({ ...prev, skillEffectList }));
      appendHistory({ step: 'skillEffect', action: 'setSkillEffect', value: { skillEffectList } });
    },
    [appendHistory]
  );

  return {
    data,
    initStep,
    switchGeograph,
    setSkill,
    setCharactorList,
    setSkillEffect,
  };
};

export default useAction;
