import React from 'react';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import { Input, notification, Form, Button, Checkbox, Space } from 'antd';
import { useEffect, useCallback, useState } from 'react';
import ResetAlert from '../components/ResetAlert';

const CheckAround = ({ value, onChange }) => (
  <span>
    <Checkbox checked={value} onChange={e => onChange(e.target.checked)} />
    在範圍內
  </span>
);

const SetCharactor = ({ value, defaultValue, onDone }) => {
  const [api, contextHolder] = notification.useNotification();
  const [form] = Form.useForm();
  const [showWarning, setShowWarning] = useState(false);
  const handleReset = useCallback(() => {
    form.resetFields();
    setShowWarning(false);
  }, [form]);

  const openNotification = useCallback(description => api.info({ message: '警告', description, placement: 'top' }), [api]);
  const handleDone = useCallback(
    () =>
      form
        .validateFields()
        .then(values => {
          const { charactor } = values;
          if (Array.isArray(charactor) === false || charactor.length === 0) openNotification('請輸入至少一名角色名稱');
          else if (charactor.some(human => human.around) === false) openNotification('請輸入至少一名角色打勾');
          else onDone(charactor.map((human, i) => ({ ...human, id: i })));
        })
        .catch(() => openNotification('請檢查欄位')),
    [form, onDone, openNotification]
  );
  useEffect(() => {
    if (value) form.setFieldsValue({ charactor: value });
  }, [value, form]);
  return (
    <div>
      {contextHolder}
      <h2>建立角色</h2>
      <Form initialValues={{ charactor: defaultValue }} form={form}>
        <Form.List name='charactor'>
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space key={key} style={{ display: 'flex', marginBottom: 8 }} align='baseline'>
                  <Form.Item {...restField} name={[name, 'name']} rules={[{ required: true, message: 'Missing name' }]}>
                    <Input placeholder='Name' />
                  </Form.Item>
                  <Form.Item {...restField} name={[name, 'around']}>
                    <CheckAround />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button type='dashed' onClick={() => add()} block icon={<PlusOutlined />}>
                  Add charactor
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>
      </Form>
      <Space>
        <Button onClick={() => setShowWarning(true)}>重設</Button>
        <Button onClick={handleDone}>確認</Button>
      </Space>
      <ResetAlert open={showWarning} onOk={handleReset} onCancel={() => setShowWarning(false)} />
    </div>
  );
};
export default SetCharactor;
