import './styles.css';
import React, { useMemo, useState } from 'react';
import useAction from './useAction';
import InitStep from './panels/InitStep';
import SwitchGeograph from './panels/SwitchGeograph';
import SetCharactor from './panels/SetCharactor';
import SelectSkill from './panels/selectSkill/SelectSkill';
import SkillEffect from './panels/SkillEffect';

export default function App() {
  const [round, setRound] = useState(1);
  const [step, setStep] = useState('initStep');
  const { data, initStep, switchGeograph, setCharactorList, setSkill, setSkillEffect } = useAction();

  const content = useMemo(() => {
    switch (step) {
      case 'initStep':
        return (
          <InitStep
            data={data}
            onDone={() => {
              initStep();
              setStep('switchGeograph');
            }}
          />
        );
      case 'switchGeograph':
        return (
          <SwitchGeograph
            data={data}
            onSwitch={() => switchGeograph()}
            onDone={() => {
              setStep('setCharactor');
            }}
          />
        );
      case 'setCharactor':
        // TODO, 如果不是最新 history，則 value 用合併後的，defaultValue 用合併前的
        return (
          <SetCharactor
            value={data.charactorList}
            defaultValue={data.charactorList}
            onDone={result => {
              setCharactorList(result);
              setStep('selectSkill');
            }}
          />
        );
      case 'selectSkill':
        return (
          <SelectSkill
            value={data}
            defaultValue={data}
            onDone={result => {
              setSkill(result.onlineSkillList, result.points);
              setStep('skillEffect');
            }}
          />
        );
      case 'skillEffect':
        return (
          <SkillEffect
            value={data}
            defaultValue={data}
            onDone={result => {
              setSkillEffect(result);
              setStep('initStep');
              setRound(prev => prev + 1);
            }}
          />
        );
      default:
        return '請檢查 step';
    }
  }, [step, data, initStep, switchGeograph, setCharactorList, setSkillEffect, setSkill]);
  return (
    <div className='App'>
      <h1>{`Round ${round}`}</h1>
      {content}
    </div>
  );
}
