import React, { useCallback, useState } from 'react';
import { Button, Space, Switch } from 'antd';
import Modal from 'antd/es/modal/Modal';
import PointsViewer from '../components/PointsViewer';

const SwitchGeograph = ({ data, onSwitch, onDone }) => {
  const [showWarning, setShowWarning] = useState(false);
  const handleSwitch = useCallback(() => {
    if (data.geograph) setShowWarning(true);
    else onSwitch();
  }, [onSwitch, data.geograph]);
  const handleOk = useCallback(() => {
    setShowWarning(false);
    onSwitch();
  }, [onSwitch]);
  return (
    <Space direction='vertical'>
      <h2>改變 Geograph 狀態</h2>
      <PointsViewer points={data.points} />
      <Switch checkedChildren='領域展開' unCheckedChildren='解除領域' checked={data.geograph} onClick={handleSwitch} />
      <Button onClick={onDone}>確認</Button>
      <Modal title='警告' onOk={handleOk} onCancel={() => setShowWarning(false)} open={showWarning}>
        當解除領域時，點數將會歸 0。您是否確定要解除呢?
      </Modal>
    </Space>
  );
};

export default SwitchGeograph;
