import { Col, InputNumber, Modal, Row, Select, Slider, Space } from 'antd';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import skillList from '../../../config/skillList.json';
import getSkill from '../../../getSkill';
import PointsViewer from '../../../components/PointsViewer';

const getMinCost = cost => POINT_TYPE.reduce((prev, key) => ({ ...prev, [key]: cost[key]?.min || 0 }), {});

const POINT_TYPE = ['sky', 'ground', 'human'];
const AddDialog = ({ points, open, onOk, onCancel }) => {
  // 如果點數不夠，disabled
  const options = useMemo(
    () =>
      skillList.map(({ id, name, cost }) => ({
        value: id,
        label: name,
        disabled: Object.keys(cost).some(key => cost[key].min > (points[key] || 0)),
      })),
    [points]
  );
  const [value, setValue] = useState({
    id: undefined,
    cost: { sky: 0, ground: 0, human: 0 },
  });

  const handleChange = useCallback(id => {
    setValue({ id, cost: getMinCost(skillList[id].cost) });
  }, []);

  const restPoints = useMemo(
    () => POINT_TYPE.reduce((prev, key) => ({ ...prev, [key]: points[key] - value.cost[key] || 0 }), {}),
    [points, value.cost]
  );

  const sliderArr = useMemo(() => {
    const pointNameMap = { sky: '天', ground: '地', human: '人' };
    const skill = getSkill(value.id);
    const { cost } = skill || {};
    const handleChange = (key, point) => setValue(prev => ({ ...prev, cost: { ...prev.cost, [key]: point } }));
    return POINT_TYPE.map(key => {
      return (
        <div key={key}>
          <h3>{pointNameMap[key]}</h3>
          <Row>
            <Col span={18}>
              <Slider
                value={value.cost[key]}
                min={cost?.[key]?.min || 0}
                max={Math.min(cost?.[key]?.max || 0, points[key]) || 0}
                onChange={point => handleChange(key, point)}
              />
            </Col>
            <Col span={4}>
              <InputNumber
                min={cost?.[key]?.min || 0}
                max={Math.min(cost?.[key]?.max || 0, points[key]) || 0}
                style={{ margin: '0 16px' }}
                value={value.cost[key]}
                onChange={point => handleChange(key, point)}
              />
            </Col>
          </Row>
        </div>
      );
    });
  }, [value, points]);

  const handleOk = useCallback(() => {
    onOk({
      restPoints,
      id: value.id,
      cost: value.cost,
    });
  }, [onOk, value, restPoints]);

  useEffect(() => {
    if (open) setValue({ id: undefined, cost: { sky: 0, ground: 0, human: 0 } });
  }, [open]);
  return (
    <Modal
      title='領域展開'
      open={open}
      onOk={handleOk}
      okButtonProps={{ disabled: [undefined, null].includes(value?.id) }}
      onCancel={onCancel}
    >
      <Space direction='vertical' style={{ width: '100%' }}>
        <Space>
          {'當前點數'}
          <PointsViewer points={points} />
        </Space>
        <Select style={{ width: '100%' }} value={value.id} options={options} onChange={handleChange} />
        <div
          style={{
            width: '100%',
            height: '100px',
            overflow: 'auto',
            // border: "1px solid #666",
            padding: '4px',
          }}
        >
          {getSkill(value.id)?.description.join('\n')}
        </div>
        <Space direction='vertical' style={{ width: '100%' }}>
          {sliderArr}
        </Space>
      </Space>
    </Modal>
  );
};
export default AddDialog;
