import { Popover } from 'antd';
import React from 'react';
import PointsViewer from './PointsViewer';

const SkillName = ({ cost, skill }) => {
  return (
    <Popover title={<PointsViewer points={cost} />} content={skill.description.join('\n')} trigger='click'>
      {skill.name}
    </Popover>
  );
};
export default SkillName;
