import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { Button, Space } from 'antd';

const InitStep = ({ data, onDone }) => {
  const { geograph } = data;
  const content = useMemo(() => {
    if (geograph) return '由於 Geograph 展開中，獲得天、地、人各 1 點';
    return '由於 Geograph 沒有展開，因此沒獲得點數';
  }, [geograph]);

  return (
    <div>
      <h2>初始階段</h2>
      <Space direction='vertical'>
        {content}
        <Button onClick={onDone}>確定</Button>
      </Space>
    </div>
  );
};
InitStep.propTypes = {
  data: PropTypes.shape({
    geograph: PropTypes.bool,
  }).isRequired,
  onDone: PropTypes.func.isRequired,
};
export default InitStep;
